<template>
  <mu-row class="banner" ref="bannerBox" @swipeleft="toLeft" @swipeRight="toRight">
    <mu-col width="100" tablet="100" desktop="100">
      <mu-icon value="keyboard_arrow_left" class="toLeft"
      @click="toLeft"
      @mouseover.native="clearTimer"
      @mouseout.native="autoPlay"></mu-icon>
      <mu-card class="pic-box">
        <transition enter-active-class="animated lightSpeedIn" leave-active-class="animated lightSpeedOut" v-for="(banner, id) in banners" :key="banner.id">
          <mu-card-media
          class="banner-item"
          v-show="id === currIndex"
          :title="banner.title"
          :subTitle="banner.subTitle">
            <div class="pic" :style="'background-image: url(' + banner.url + ')'"></div>
          </mu-card-media>
        </transition>
      </mu-card>
      <mu-icon value="keyboard_arrow_right" class="toRight"
      @click="toRight"
      @mouseover.native="clearTimer"
      @mouseout.native="autoPlay"></mu-icon>
    </mu-col>
  </mu-row>
</template>

<script>
  export default {
    name: 'banner',
    props: {
      banners: {
        type: Array,
        default: []
      }
    },
    data () {
      return {
        currIndex: 0,
        timer: null,
        dur: 3000,
        show: true,
        num: 0
      }
    },
    mounted () {
      this.num = this.banners.length
      this.width = this.$refs.bannerBox.$el.clientWidth
      this.autoPlay()
    },
    methods: {
      autoPlay () {
        this.timer = setInterval(() => {
          this.currIndex++
          this.currIndex %= this.num
        }, this.dur)
      },
      toLeft () {
        this.currIndex = --this.currIndex < 0 ? this.num - 1 : this.currIndex
      },
      toRight () {
        this.currIndex++
        this.currIndex %= this.num
      },
      clearTimer () {
        clearInterval(this.timer)
      }
    }
  }
</script>

<style lang='less' scoped>
  .banner {
    position: relative;
    height: 500px;
  }
  .banner-item {
    position: absolute;
    width: 100%;
    height: inherit;
  }
  .pic-box {
    width: 100%;
    height: 500px;
    overflow: hidden;
  }
  .pic-box>div {
    transition: transform 1s linear
  }
  .pic {
    height: 500px;
    background-position: 50% 50%;
  }
  .mixin-arrow{
    position: absolute;
    top: 50%;
    margin-top: -50px;
    color: #333;
    text-shadow: #333 0 0 10px;
    font-size: 100px;
    z-index: 2;
    cursor: pointer;
  }
  .toLeft {
    .mixin-arrow();
    left: -100px;
  }
  .toRight {
    .mixin-arrow();
    right: -100px;
  }
</style>



