<template>
  <div id="app">
    <mu-appbar title="Title">
      <mu-icon-button icon="menu" slot="left" label="toggle drawer" @click="toggle()"/>
      <mu-drawer left :open="open" @close="toggle()">
        <mu-appbar title="Muse UI"/>
        <mu-list>
          <mu-list-item title="Menu Item 1"/>
          <mu-list-item title="Menu Item 2"/>
          <mu-list-item title="Menu Item 3"/>
          <mu-list-item @click.native="open = false" title="Close"/>
        </mu-list>
      </mu-drawer>
      <mu-text-field icon="search" class="appbar-search-field"  slot="right" hintText="请输入搜索内容"/>
      <mu-flat-button color="white" label="flat Button" slot="right"/>
    </mu-appbar>
    <mu-row>
      <mu-col width="100" tablet="80" desktop="65" class="center">
        <router-view></router-view>
      </mu-col>
    </mu-row>
  </div>
</template>
<script>
  export default {
    name: 'app',
    data () {
      return {
        open: false
      }
    },
    methods: {
      toggle () {
        this.open = !this.open
      }
    }
  }
</script>
<style lang="less">
@import url(./assets/less/font);
@import url(./assets/less/vars);
.center {
  margin: @base-margin auto;
}
.appbar-search-field{
  color: #FFF;
  margin-bottom: 0;
  &.focus-state {
    color: #FFF;
  }
  .mu-text-field-hint {
    color: fade(#FFF, 54%);
  }
  .mu-text-field-input {
    color: #FFF;
  }
  .mu-text-field-focus-line {
    background-color: #FFF;
  }
}
</style>


