<template>
  <div class="home">
    <banner :banners="banners"></banner>
  </div>
</template>

<script>
import Banner from '@/components/banner'

export default {
  name: 'home',
  components: {
    Banner
  },
  data () {
    return {
      banners: [
        {title: 'Banner One', subTitle: 'banner', url: 'static/img/1.jpg'},
        {title: 'Banner Two', subTitle: 'banner', url: 'static/img/2.jpg'},
        {title: 'Banner Three', subTitle: 'banner', url: 'static/img/3.jpg'}
      ]
    }
  }
}
</script>

<style>

</style>



